  font-family: "poppins";
}
body {
  background-color: rgb(0, 0, 0);
  color: white;
}
form > button {
  width: 10rem;
}

button {
  width: fit-content;
  padding: 5px 10px;
}
.sidebar {
  grid-column: 1 / 2;
  border-radius: 15px;
  width: 20vw;
  height: 80vh;
  background-color: rgb(0, 0, 0);
  color: white;
  border: 2px solid white;
  padding: 20px;
  justify-items: center;
}
header {
  justify-items: center;
}

.projectDivTemplate {
  width: 19vw;
  height: 5vh;
  display: flex;
  padding: 10px;
  justify-content: space-between;
  margin: 10px;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 10px;
  transition: 0.1s ease-in;
  align-items: center;
}
.projectDivTemplate:hover {
  transform: scale(1.05);
  background-color: rgb(83, 78, 78);
  border-radius: 10px;
}

.projectTitle {
  display: flex;
  font-size: 1.25rem;
  padding: 5px 25px;
  cursor: pointer;
  background-color: rgb(0, 0, 0);
  color: white;
  outline: none;
  border: none;
  width: 16vw;
  height: 5vh;
  transition: 0.1s ease-in;
}

.projectTitle:hover {
  background-color: rgb(83, 78, 78);
  border-radius: 10px;
}
.deleteProjectBtn {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  font-size: 1.25rem;
  background-size: contain;
  justify-content: space-between;
  background-repeat: no-repeat;
  /* display: inline-block; */
  height: 3.5rem;
  width: 3.5rem;
  border-radius: 30%;
  cursor: pointer;
  padding: 0px;
  border: none;
  transition: 0.1s ease-in;
}
.deleteProjectBtn:hover {
  border-radius: 30%;
  box-shadow: 0px 0px 50px red;
}
#createProject {
  font-size: 1.25rem;
  cursor: pointer;
  background-color: black;
  color: white;
  border: none;
  padding: 5px;
  transition: 0.1s ease-in;
}
#createProject:hover {
  color: black;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0px 0px 25px cyan;
}
.deleteTodoBtn {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  font-size: 1.25rem;
  background-size: contain;
  justify-content: space-between;
  background-repeat: no-repeat;
  /* display: inline-block; */
  height: 3.5rem;
  width: 3.5rem;
  border-radius: 30%;
  cursor: pointer;
  padding: 0px;
  border: none;
  transition: 0.1s ease-in;
}
.deleteTodoBtn:hover {
  border-radius: 30%;
  box-shadow: 0px 0px 50px red;
}
.editTodo {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
