  width: fit-content;
  padding: 5px 10px;
  transition: 0.1s ease-in;
}
#createTodo:hover {
  box-shadow: 0px 0px 25px cyan;
  border-radius: 5px;
  background-color: white;
  color: black;
}
.defaultProject {
  width: 19vw;
  height: 7vh;
  border: 3px solid white;
  display: flex;
  font-size: 1.25rem;
