}
dialog {
  outline: none;
  border: none;
}
input {
  outline: none;
  padding: 5px 15px;
  border-radius: 15px;
  border: 3px solid white;
  background-color: black;
  color: white;
  font-size: 1.25rem;
}
textarea {
  font-size: 1.25rem;
  background-color: black;
  color: white;
}
#dueDate {
  background-color: beige;
  color: black;
}
dialog {
  border-radius: 20px;
}
#projectForm {
