  border-radius: 15px;
  background-color: black;
  color: white;
  width: fit-content;
  font-size: 1.25rem;
  position: relative;
  gap: 10px;
  display: flex;
  flex-direction: column;
  width: fit-content;
}
#projectForm > button {
  background-color: black;
  color: white;
  font-size: 1.25rem;
  border: 3px solid white;
  border-radius: 15px;
}
#projectForm > button:hover {
  cursor: pointer;
  background-color: white;
