  gap: 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: fit-content;
}
#formTodo > button,
#updateformTodo > button {
  background-color: black;
  color: white;
  font-size: 1.25rem;
  border: 3px solid white;
  border-radius: 15px;
}
#formTodo > button:hover,
#updateformTodo > button:hover {
  cursor: pointer;
  background-color: white;
  color: black;
  box-shadow: 0px 0px 50px rgb(255, 209, 5);
}
select {
  padding: 5px;
  border-radius: 15px;
  background-color: black;
  color: white;
  font-size: 1.25rem;
}

.ultimateContainer {
  display: grid;
  grid-template-columns: 20vw 5vw 75vw;
}
